/*variables for site-wide themes and styling*/

//
$font-stack:  fira-sans-2, sans-serif;
$heading-size: 7rem;
$sub-heading-size: 2rem;
$link-size: 1.5rem;

//colour theme
$color-primary: white;
$color-accent: lightgrey;
$color-base: black;
$color-link: #7eab29;
$color-facebook: #3b5998;

//widths to keep everything on a grid
$full-width: 100vw;
$half-width: 50vw;
$third-width: 33.33vw;
$quarter-width: 25vw;
$full-height: 100vh;
$half-height: 50vh;
$third-height: 33.33vh;
$quarter-height: 25vh;

//image links
$header-image: url("/../app/images/topimage.png");
$firstTransition-image: url("/../app/images/topimage.png");
$photography-image: url("/../app/images/photography.png");
$design-image: url("/../app/images/design.JPG");
$webDesign-image: url("/../app/images/web.png");

//transition for smooth fades on links
$linktransition: all .5s cubic-bezier(1,.25,0,.75) 0s;



//set all defaults to normal font-weight
h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

body {
  background-color: $color-base;
  margin: 0;
  color: $color-primary;
  font-family: fira-sans-2, sans-serif; }

a {
  text-decoration: none;
  color: $color-primary; }

.wrapper {
  height: $full-height;
  overflow-x: hidden;
  overflow-y: auto;
  perspective: 2px; }

#logo {
  display: flex;
  height: 40vh;
  width: $full-width; }

nav {
  position: fixed;
  z-index: 100;
  top: 0vw;
  float: right;
  padding-right: 2vw;
  width: $full-width;
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgba(0,0,0,0) 30%, rgba(0,0,0,1) 70%); }

#menu {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  list-style-type: none;
  text-decoration: none; }

#menulogo {
  filter: invert(100%);
  height: 10vw;
  padding: 1vw 45vw 1vw 1vw;
  display: inline-block; }

.menuitems {
  display: inline-block;
  padding-right: 0;
  margin-left: 2vw;
  letter-spacing: .2vw;
  font-size: $link-size; }

svg {
	display: block; }

.c-nav__item {
	a {
		display: inline-block;
		text-decoration: none;
		color: $color-primary;
		transition: $linktransition; }
	svg {
		pointer-events: none {
        transition: $linktransition;
        outline: solid 1px $color-accent; } }
	path {
    transition: stroke-dasharray 0.5s, stroke-dashoffset 0.5s, opacity 0.5s;
    &:last-child {
      opacity: 0.2; } }
	a {
		&:hover,
		&:focus {
			color: $color-link; } }
	a:hover + svg,
	a:focus + svg {
		opacity: 1;
		path {
			stroke-dashoffset: 0; } } }

#facebook {
	a {
		display: inline-block;
		text-decoration: none;
		color: $color-primary;
		transition: $linktransition; }
	a {
		&:hover,
		&:focus {
			color: $color-facebook; } } }

#caret {
  display: flex;
  margin: 4vh auto;
  img {}
  height: 3rem;
  width: 3rem; }

#instagram {
	a {
		&:hover,
		&:focus {
      background: radial-gradient(circle farthest-corner at 35% 90%, #fec564, transparent 50%), radial-gradient(circle farthest-corner at 0 140%, #fec564, transparent 50%), radial-gradient(ellipse farthest-corner at 0 -25%, #5258cf, transparent 50%), radial-gradient(ellipse farthest-corner at 20% -50%, #5258cf, transparent 50%), radial-gradient(ellipse farthest-corner at 100% 0, #893dc2, transparent 50%), radial-gradient(ellipse farthest-corner at 60% -20%, #893dc2, transparent 50%), radial-gradient(ellipse farthest-corner at 100% 100%, #d9317a, transparent), linear-gradient(#6559ca, #bc318f 30%, #e33f5f 50%, #f77638 70%, #fec66d 100%);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      transition: $linktransition; } } }

.fullScreen {
  width: $full-width;
  height: 100vh; }

.smallTransition {
  min-height: $third-height;
  width: $full-width; }

#portfolioTransition {
  background: $firstTransition-image 50% 0 repeat fixed;
  display: flow-root;
  h1 {
    font-family: $font-stack;
    font-size: $heading-size;
    padding-left: 5vw;
    line-height: 1vw; }
  img {
    padding-left: 5vw; } }

#portfolio {
  width: $full-width;
  height: 120vw; }

.largeTransition {
  width: 100vw;
  height: 40vw; }

.sectionUnderline {
  width: 20vw; }

#topimage {
  height: 50vh;
  width: 100vw;
  background-image: $header-image;
  background-size: cover;
  display: flex;
  align-items: center; }

#topGrid {
  height: $half-height;
  width: $full-width; }

.button {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(20px);
  text-align: center;
  margin: auto;
  align-items: center;
  line-height: relative;
  a {
    font-size: $sub-heading-size; } }

#photography {
  height: $half-height;
  width: $third-width;
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  filter: brightness(0.5);
  background-image: $photography-image;
  background-size: cover;
  display: flex;
  align-items: center;
  margin: auto;
  i {
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: $photography-image;
    background-size: 43.3vw;
    background-position: -9vw;
    text-align: -webkit-center;
    padding-top: 10vh; }
  a {
    font-size: 3rem;
    font-family: fira-sans-condensed, sans-serif;
    font-weight: 400;
    display: -webkit-box;
    line-height: initial; } }


#design {
  height: $half-height;
  width: $third-width;
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  filter: brightness(0.5);
  background-image: $design-image;
  background-size: cover;
  display: flex;
  align-items: center;
  margin: auto;
  i {
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: $design-image;
    background-size: 43.3vw;
    background-position: -9vw;
    text-align: -webkit-center;
    padding-top: 10vh; }
  a {
    font-size: 3rem;
    font-family: fira-sans-condensed, sans-serif;
    font-weight: 400;
    display: -webkit-box;
    line-height: initial; } }


#web {
  height: $half-height;
  width: $third-width;
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  filter: brightness(0.5);
  background-image: $webDesign-image;
  background-size: cover;
  display: flex;
  align-items: center;
  margin: auto;
  i {
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: $webDesign-image;
    background-size: 43.3vw;
    background-position: -9vw;
    text-align: -webkit-center;
    padding-top: 10vh; }
  a {
    font-size: 3rem;
    font-family: fira-sans-condensed, sans-serif;
    font-weight: 400;
    display: -webkit-box;
    line-height: initial; } }

.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0;
  align-items: center; }

.column {
  flex: 33.33%;
  max-width: 33.33%;
  padding: 0 0;
  align: center; }

.column img {
  margin-top: 0;
  vertical-align: middle;
  width: 100%; }
